


























import segmentedControl from '../../../components/segmented-control.vue';
import handelview from '../../../components/handel-view.vue';
import echart_line_view from '../../../components/card_type/echart_line.vue';
export default {


	data() {
		return {
			luyouData_ziduan: null,
			luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
			],
			show_flag:false,
			list_id: null,
			items: [
				'折线图',
				'改进',
			],
			height: 0,
			current: 0,
		}
	},
	components: {
		handelview,
		segmentedControl,
		echart_line_view,
	},

	onLoad:  function(option) {
		var that = this;
		this.height = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
		console.log("list_id:" + option.list_id)
		this.list_id = option.list_id;
		setTimeout(() => this.deal_with_data(), 500);
	},

	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		
		get_hot_card_list() {
			var that = this;
			return new Promise((res) => {
				uni.getStorage({
					key: 'hot_card_list',
					success: function(res1) {
						that.luyouData = res1.data.data;
						res('ss');
					},
				});
			})

		},
		get_hot_card_list_ziduan() {
			var that = this;
			return new Promise((res) => {
				uni.getStorage({
					key: 'uni_app_hot_list_ziduan',
					success: function(res1) {
						that.luyouData_ziduan = res1.data.data;
						res('ss');
					},
				});
			})

		},
		tishi: function() {
			uni.showModal({
				title: '说明',
				content: this.luyouData[this.list_id].tishi_content,
				showCancel: false,
			})
		},
		refreash: function() {
			var url_self = this.luyouData[this.list_id].url.split('/')
			// console.log(url_self[url_self.length-1])
			uni.reLaunch({
				url: url_self[url_self.length - 1] + "?list_id=" + this.list_id,
			})
		},
		deal_with_data: async function() {
			const that = this;
			let getdataresult0 = await that.get_hot_card_list();
			let getdataresult1 = await that.get_hot_card_list_ziduan();
			console.log("list_id",this.list_id)
			console.log("luyouData",this.luyouData)
			console.log("luyouData_ziduan",this.luyouData_ziduan)
			that.show_flag=true;
		},




	}
}



